import React, { Fragment } from "react";
import { Text } from "@fluentui/react";
import styles from "./Timeline.module.css";

const textStyles = (enabled) => {
  return {
    root: {
      color: `${enabled ? "#000000" : "#B8B8B8B8"}`,
      fontWeight: `${enabled ? "700" : "400"}`,
    },
  };
};

const Timeline = ({ dataSrc, handleChange }) => {
  return (
    <>
      {dataSrc.map((stage, index) => {
        const { title, enabled, timePeriod } = stage;
        const isSecondLast = index === dataSrc.length - 2;
        const isLast = index === dataSrc.length - 1;
        return (
          <Fragment key={`timeline_row_${index + 1}`}>
            <Text
              as="p"
              styles={textStyles(enabled)}
              variant="medium"
              className={styles.timelineItems}
            >
              {title}
            </Text>
            <div
              onClick={() => handleChange(index)}
              className={`${styles.timelineDotContainer} ${
                enabled ? styles.enabled : ""
              }`}
            >
              <div
                className={`${styles.timelineVerticalLine} ${
                  isSecondLast ? styles.displayNone : ""
                }${isLast ? styles.timelineVerticalLineReverse : ""}`}
              />
              <div
                className={`${styles.timelineDot} ${
                  enabled ? styles.enabled : ""
                }`}
              ></div>
            </div>
            <Text as="p" variant="smallPlus" className={styles.timelineItems}>
              {timePeriod}
            </Text>
          </Fragment>
        );
      })}
    </>
  );
};

export default Timeline;
