import { Text } from "@fluentui/react";
import React from "react";
import { fieldLabelStyles } from "../../containers/EditCustomisation";

const PinnedLabel = (props) => {
  const { labelOne, labelTwo, gridGap = "5px" } = props;
  return (
    <div style={{ display: "grid", gridTemplateColumns: "35% 50%", gap: gridGap }}>
      <Text block styles={fieldLabelStyles} as="p">
        {labelOne}
      </Text>
      <Text block styles={fieldLabelStyles} as="p">
        {labelTwo}
      </Text>
    </div>
  );
};

export default PinnedLabel;
