import { FontSizes, Stack, Text } from "@fluentui/react";
import React from "react";

const stackStyles = (length) => {
  const widthCalculated = (100 / length).toFixed(2);
  const style = {
    root: {
      width: `${widthCalculated}%`,
    },
  };
  return style;
};

const labelStyles = {
  root: {
    fontSize: FontSizes.size12,
    color: "#B8B8B8",
    textTransform: "uppercase",
    lineHeight: "15px",
    marginTop: "0px",
    marginBottom: "5px",
  },
};

const valueStyles = {
  root: {
    color: "#000000",
    fontSize: FontSizes.size16,
    width: "95%",
  },
};

const textPlain = ({ label, value, length = 0 }) => {
  return (
    <Stack verticalAlign={"center"} horizontalAlign={"start"} styles={stackStyles(length, true)}>
      {/* Lable Name */}
      <Text as="p" block styles={labelStyles}>
        {label}
      </Text>
      {/* Field Value Name */}
      <Text as="p" block nowrap styles={valueStyles}>
        {value}
      </Text>
    </Stack>
  );
};

const componentTypes = {
  plainText: (props) => textPlain(props),
};

const CardCell = ({ length, cellDetails }) => {
  const { label, value } = cellDetails;
  return componentTypes["plainText"]({ label, value, length });
};

export default CardCell;
