import { Dropdown, Text } from "@fluentui/react";
import React from "react";

const criteriaStyleWrapper = {
  display: "flex",
  flexDirection: "row",
  width: "100%",
  alignItems: "center",
  justifyContent: "space-between",
};

const dropDownStyles = {
  root: {
    minWidth: "250px",
  },
};

const LabeledDropdown = (props) => {
  const { fieldName, dropDownOptions } = props;
  const { placeHolder, options } = dropDownOptions;
  console.log(dropDownOptions, "The options for the dropdown received are");
  return (
    <div style={criteriaStyleWrapper}>
      <Text style={{ fontSize: "14px" }} as="p" block>
        {fieldName}
      </Text>
      <Dropdown placeholder={placeHolder} styles={dropDownStyles} options={options} />
    </div>
  );
};

export default LabeledDropdown;
