import { Dropdown, TextField } from "@fluentui/react";
import React from "react";
import CustomIconButton from "../button/CustomIconButton";

const CustomField = () => {
  return (
    <>
      <Dropdown
        placeholder="Select Field Type"
        options={[
          { key: "date", text: "Date" },
          { key: "plainText", text: "Plain Text" },
        ]}
      />
      <TextField placeholder="Enter Field Name" />
      <CustomIconButton backgroundColor="#0078D4" iconName="Settings" iconColor="white" />
      <CustomIconButton backgroundColor="#C90000" iconName="Delete" iconColor="white" />
    </>
  );
};

export default CustomField;
