import React, { useState } from "react";
import styles from "./CaseCard.module.css";
import { Text } from "@fluentui/react";

import CardCell from "../cardCell/CardCell";
import Timeline from "../timeline/Timeline";

const areaThreeMockArray = [
  { label: "Area of Law", value: "Xxxxxxxx" },
  { label: "Pratice Group", value: "Xxxxxxxx" },
  { label: "Cause of Action", value: "Xxxxxxx" },
];

const timeLineMock = [
  { title: "Admin Charges", enabled: false, timePeriod: "30d" },
  { title: "Early Case Assessment", enabled: false, timePeriod: "27d" },
  { title: "Pleadings", enabled: false, timePeriod: "25d" },
  { title: "Fact Discovery", enabled: false, timePeriod: "12d" },
  { title: "Summary Judgment", enabled: true, timePeriod: "1d" },
];

const CaseSectionTwo = () => {
  const [timeLineDataSrc, setTimeLineDataSrc] = useState(timeLineMock);
  const handleTimelineChange = (index) => {
    let items = [...timeLineDataSrc];
    let item = { ...items[index] };
    item.enabled = !item.enabled;
    items[index] = item;
    setTimeLineDataSrc(items);
  };
  return (
    <div className={styles.sectionTwo}>
      {/* START: Department */}
      <div className={styles.departmentContainer}>
        <Text as="p" variant="small" className={styles.departmentText}>
          Litigation
        </Text>
      </div>
      {/* END: Department */}

      {/* START: Area 3 */}
      <div className={styles.caseAreaThree}>
        {/* START: Area 3 Fields */}
        <div className={styles.caseAreaThreeFields}>
          {areaThreeMockArray.map((cellInfo, index) => {
            const keyName = `Cell_AREA_THREE_${index + 1}`;
            return (
              <CardCell key={keyName} cellDetails={cellInfo} length={areaThreeMockArray.length} />
            );
          })}
        </div>
        {/* END: Area 3 Fields */}
        {/* START: Area 3 Timeline */}
        <div className={styles.caseAreaThreeTimeline}>
          <Timeline
            dataSrc={timeLineDataSrc}
            handleChange={handleTimelineChange}
          />
        </div>
        {/* END: Area 3 Timeline */}
      </div>
      {/* END: Area 3 */}
    </div>
  );
};

export default CaseSectionTwo;
