import React from "react";
import styles from "./CaseCard.module.css";
import { Depths } from "@fluentui/theme";
import CaseSectionOne from "./CaseSectionOne";
import CaseSectionTwo from "./CaseSectionTwo";
import CaseSectionThree from "./CaseSectionThree";

const CaseCard = () => {
  return (
    <div style={{ boxShadow: Depths.depth16 }} className={styles.caseCard}>
      <CaseSectionOne />
      <CaseSectionTwo />
      <CaseSectionThree />
    </div>
  );
};

export default CaseCard;
