import React from "react";
import CaseCard from "../lib/card/caseCard/CaseCard";

const caseListForCards = [1, 2, 3];

const CaseList = () => {
  return (
    <>
      {caseListForCards.map((number, index) => {
        return <CaseCard key={`case_card_${number}`} />;
      })}
    </>
  );
};

export default CaseList;
