import { Text } from "@fluentui/react";
import React from "react";
import CaseCard from "../lib/card/caseCard/CaseCard";
import { formTitleStyles } from "./EditCustomisation";
import styles from "./CustomCardLayout.module.css";
import AreaRenderer from "./AreaRenderer";

const CustomCardLayout = () => {
  return (
    <div style={{ marginBottom: "35px" }}>
      <Text styles={formTitleStyles} as="h2">
        Custom Card Layout
      </Text>
      <div className={styles.caseCardLayoutWrapper}>
        <CaseCard />
      </div>
      <div className={styles.customAreaWrapper}>
        <AreaRenderer areaName="Area 1" />
        <AreaRenderer areaName="Area 2" />
        <AreaRenderer areaName="Area 3" />
        <AreaRenderer areaName="Area 4" />
      </div>
    </div>
  );
};

export default CustomCardLayout;
