import React from "react";
import styles from "./CaseCard.module.css";

import CardCell from "../cardCell/CardCell";
import CardHeader from "./CardHeader";
import CaseStatus from "../../generic/caseStatus/CaseStatus";
import { Icon, Text } from "@fluentui/react";

const areaOneMockArray = [
  { label: "jurisdiction", value: "Federal", type: "plainText" },
  { label: "venue", value: "Pennsylvania", type: "plainText" },
  { label: "court", value: "District of Pennsylvania", type: "plainText" },
  { label: "value Four", value: "Value for four", type: "plainText" },
  { label: "five", value: "Value for fine", type: "plainText" },
  { label: "six", value: "Value for six", type: "plainText" },
];

const areaTwoMockArray = [
  {
    label: "summary",
    value:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.",
    type: "plainTextfullWidth",
  },
];

const CaseSectionOne = () => {
  return (
    <div className={styles.sectionOne}>
      <CardHeader />
      <div className={styles.caseAreaOne}>
        {areaOneMockArray.map((cellInfo, index) => {
          // This renders the cardcell component for area one
          const keyName = `CELL_AREA_ONE_${index + 1}`;
          return <CardCell key={keyName} cellDetails={cellInfo} length={areaOneMockArray.length} />;
        })}
      </div>
      <div className={styles.caseAreaTwo}>
        {areaTwoMockArray.map((cellInfo, index) => {
          // This renders the cardcell component for area two
          const keyName = `CELL_AREA_TWO_${index + 1}`;
          return <CardCell key={keyName} cellDetails={cellInfo} length={areaTwoMockArray.length} />;
        })}
      </div>
      <div className={styles.caseAreaFooter}>
        <CaseStatus caseStatus="active" />
        <div className={styles.caseFooterWrapper}>
          <Icon iconName="Attach" className={styles.caseFooterIcon} />
          <Text className={styles.caseFooterHighlightedText}>{1}</Text>
          <Text className={styles.footerInfoStyles}>Documents</Text>
        </div>
        <div className={styles.caseFooterWrapper}>
          <Icon
            iconName="EventAccepted"
            className={styles.caseFooterIcon}
            style={{ transform: "rotate(0deg)" }}
          />
          <Text className={styles.caseFooterHighlightedText}>{1}</Text>
          <Text className={styles.footerInfoStyles}>Tasks Remaining</Text>
        </div>
        <div className={styles.caseFooterWrapper}>
          <Icon
            iconName="Chat"
            className={styles.caseFooterIcon}
            style={{ transform: "rotate(0deg)" }}
          />
          <Text className={styles.caseFooterHighlightedText}>{1}</Text>
          <Text className={styles.footerInfoStyles}>Notes</Text>
        </div>
      </div>
    </div>
  );
};

export default CaseSectionOne;
