import { Dropdown } from "@fluentui/react";
import React from "react";
import CustomIconButton from "../button/CustomIconButton";

const AreaField = () => {
  return (
    <>
      <Dropdown
        placeholder="Select Field"
        options={[
          { key: "nextStatusCall", text: "Next Status Call" },
          { key: "lastUpdated", text: "Last Updated" },
          { key: "jurisdiction", text: "Jusrisdiction" },
        ]}
      />
      <Dropdown
        placeholder="Select Style"
        options={[
          { key: "highlightBox", text: "Highlight Box" },
          { key: "plainText", text: "Plain Text" },
          { key: "plainTextFull", text: "Plain Text Full" },
        ]}
      />
      <CustomIconButton backgroundColor="#C90000" iconName="Delete" iconColor="white" />
    </>
  );
};

export default AreaField;
