import { Separator, Text, TextField } from "@fluentui/react";
import React from "react";
import ButtonWithIcon from "../lib/generic/button/ButtonWithIcon";
import CustomField from "../lib/generic/customField/CustomField";
import LabeledDropdown from "../lib/generic/labeledDropdown/LabeledDropdown";
import PinnedLabel from "../lib/generic/PinnedLabel";

export const formTitleStyles = {
  root: {
    fontSize: "24px",
    color: "#A5A5A5",
  },
};

export const fieldLabelStyles = {
  root: {
    fontSize: "12px",
    color: "#B8B8B8",
    textTransform: "uppercase",
  },
};

const formWrapper = {
  marginTop: "20px",
  paddingLeft: "10px",
};

const textAreaStyle = {
  root: {
    maxWidth: "600",
  },
  fieldGroup: {
    border: "solid 1px #D6D6D6",
    borderRadius: "5px",
    marginTop: "5px",
  },
};

const criteriaOptions = [
  {
    fieldName: "Department",
    dropDownOptions: {
      placeHolder: "Select A Department",
      options: [
        { key: "fruits_name", text: "Fruits Name" },
        { key: "fruits", text: "Fruits" },
        { key: "fruits_sub_name", text: "Fruits Sub Name" },
      ],
    },
  },
  {
    fieldName: "Area Of Law",
    dropDownOptions: {
      placeHolder: "Select An Area of Law",
      options: [
        { key: "area_of_law_01", text: "Area of Law 01" },
        { key: "area_of_law_02", text: "Area of Law 02" },
        { key: "area_of_law_03", text: "Area of Law 03" },
        { key: "area_of_law_04", text: "Area of Law 04" },
      ],
    },
  },
  {
    fieldName: "Sub area of law",
    dropDownOptions: {
      placeHolder: "Select a sub area of law",
      options: [
        { key: "sub_area_01", text: "Sub Area 01" },
        { key: "sub_area_02", text: "Sub Area 02" },
        { key: "sub_area_03", text: "Sub Area 03" },
        { key: "sub_area_04", text: "Sub Area 04" },
      ],
    },
  },
];

const seperatorStyles = {
  root: {
    ":before": {
      backgroundColor: "#E8E8E8",
      height: "2px",
    },
  },
};

const customFieldsWrapperStyles = {
  display: "grid",
  gridTemplateColumns: "35% 44% 8% 8%",
  minHeight: "200px",
  gridGap: "10px 5px",
};

const EditCustomisation = () => {
  return (
    // The content at the bottom should not be clutched to the end of screen leave the 35px margin
    <div style={{ marginBottom: "35px" }}>
      <Text styles={formTitleStyles} as="h2">
        Target Details
      </Text>
      {/* START: Renders the target name and the description */}
      <div style={formWrapper}>
        <Text block styles={fieldLabelStyles}>
          Target Name
        </Text>
        <TextField styles={textAreaStyle} />
        <TextField multiline styles={textAreaStyle} />
      </div>
      {/* END: Renders the target name and the description */}
      <Separator styles={seperatorStyles} />
      {/* START: Renders the criteria fields for editing */}
      <Text styles={formTitleStyles} as="h2">
        Criteria
      </Text>
      <div style={formWrapper}>
        {criteriaOptions.map((criteriaValues, index) => {
          const keyName = `LABLED_CRITERIA_KEY_${index + 1}`;
          const { fieldName, dropDownOptions } = criteriaValues;
          return (
            <LabeledDropdown
              key={keyName}
              fieldName={fieldName}
              dropDownOptions={dropDownOptions}
            />
          );
        })}
      </div>
      <Separator styles={seperatorStyles} />
      {/* END: Renders the criteria fields for editing */}
      {/* START: Renders the criteria fields for editing */}
      <Text styles={formTitleStyles} as="h2">
        Custmom Fields
      </Text>
      <div style={formWrapper}>
        <PinnedLabel labelOne="Field Type" labelTwo="Field Name" />
        {/* This area renders the custom fields added to the card */}
        <div style={customFieldsWrapperStyles}>
          <CustomField />
          <CustomField />
          <CustomField />
          <CustomField />
        </div>
        <ButtonWithIcon iconName="Add" name="Add field" />
      </div>
    </div>
  );
};

export default EditCustomisation;
