import React from "react";
import { Persona, PersonaSize, PersonaPresence } from "@fluentui/react";
import styles from "./ContactCard.module.css";

const ContactCard = ({ contactDetails, highlighted }) => {
  const { firstName, lastName, role, presence, imageUrl } = contactDetails;
  return (
    <div
      className={`${styles.contactCardContainer} ${
        highlighted ? styles.contactCardContainerHighlighted : ""
      }`}
    >
      <Persona
        imageUrl={imageUrl}
        imageInitials={`${firstName[0]}${lastName[0]}`}
        text={`${firstName} ${lastName}`}
        secondaryText={role}
        size={highlighted ? PersonaSize.size48 : PersonaSize.size40}
        presence={PersonaPresence[presence]}
      />
    </div>
  );
};

export default ContactCard;
