import React from "react";
import { Icon } from "@fluentui/react/lib/Icon";
import { Text } from "@fluentui/react";
import styles from "../caseCard/CaseCard.module.css";

const BookMarkIcon = () => (
  <Icon style={{ transform: "translate(5px, 10px) scale(2)" }} iconName="AddBookmark" />
);

const CardHeader = () => {
  return (
    <div className={styles.cardHeader}>
      {/* START: Displays the bookmark icon and the button action  */}
      <div className={styles.bookmarkSection}>
        <BookMarkIcon />
      </div>
      {/* END: Displays the bookmark icon and the button action  */}
      {/* START: Renders the card header and the sub heading */}
      <div className={styles.cardHeaderWrapper}>
        {/* START: Renders the first half of the card information highlighted as bold */}
        <div className={styles.cardHeading}>
          {/* <h1>This is the matter name text</h1> */}
          <Text as="h1" variant="xxLarge">
            This is the matter name text
          </Text>
          <Text as="h2" variant="xxLargePlus">
            00123456
          </Text>
        </div>
        {/* END: Renders the first half of the card information highlighted as bold */}
        {/* START: Renders the second half of the card information highlighted as light colored */}
        <div className={styles.cardSubHeading}>
          <Text as="h3" variant="xLarge">
            ABC Client Company Inc.
          </Text>
          <Text as="h2" variant="xLarge">
            012345.00
          </Text>
        </div>
        {/* END: Renders the second half of the card information highlighted as light colored */}
      </div>
      {/* END: Renders the card header and the sub heading */}
    </div>
  );
};

export default CardHeader;
