import { Separator, Text } from "@fluentui/react";
import React from "react";
import CustomCardLayout from "../containers/CustomCardLayout";
import EditCustomisation from "../containers/EditCustomisation";

const headingStyles = {
  fontSize: "32px",
  color: "#000000",
};

const CaseModifications = () => {
  return (
    <div style={{ paddingLeft: "15px", backgroundColor: "#FFFFFF" }}>
      <div className="modificationsTitle">
        <Text style={headingStyles} variant="" as="h1">
          Edit Customization
        </Text>
      </div>
      <div className="modificationsWrapper">
        {/* Customization for the fields for law of hierarchy */}
        <EditCustomisation />
        <Separator vertical style={{ width: "50px" }} />
        {/* Custom Card Layout edit layout */}
        <CustomCardLayout />
      </div>
    </div>
  );
};

export default CaseModifications;
