import React from "react";
import logo from "./logo.svg";
import "./App.css";
import CaseList from "./components/pages/CaseList";
import { initializeIcons } from "@uifabric/icons";
import { getTheme } from "@fluentui/react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import CaseModifications from "./components/pages/CaseModifications";

const theme = getTheme();
initializeIcons();

function App() {
  return (
    <Router>
      <div data-testid="app" className="applicationContainer">
        <header style={{ boxShadow: theme.effects.elevation16 }} className="applicationHeader">
          <img src={logo} className="applicationLogo" alt="logo" />
          <p>This will be the header of the site</p>
        </header>
        <Switch>
          <Route exact path="/">
            <div className="caseListWrapper">
              <CaseList />
            </div>
          </Route>
          <Route exact path="/case-customization">
            <CaseModifications />
          </Route>
        </Switch>
      </div>
    </Router>
  );
}

export default App;
