import React from "react";
import styles from "./CaseStatus.module.css";
import { Icon } from "@fluentui/react/lib/Icon";
import { Text } from "@fluentui/react";

// const iconWrapperStyle = (status) => {
//   const color = status === "pending" ? "#0078D4" : status === "active" ? "#11A300" : "#C90000";
//   return {
//     backgroundColor: color,
//   };
// };
// const pendingIconStyle = { color: "white", scale: "0.7", marginLeft: "3px" };
// const closedIconStyle = { color: "white", scale: "0.7" };
// const activeIconStyle = { color: "white", scale: "0.7", marginLeft: "2px" };

const iconStyles = {
  active: {
    iconName: "CaretRightSolid8",
    color: "#11A300",
    iconLabel: "Active",
    style: {
      color: "white",
      transform: "scale(0.7) translate(2px, 2px)",
    },
  },
  pending: {
    iconName: "SkypeClock",
    color: "#0078D4",
    iconLabel: "Pending",
    styleName: "caseStatusPendingIcon",
    style: {
      color: "white",
      transform: "scale(0.7) translate(2px, 1px)",
    },
  },
  closed: {
    iconName: "Remove",
    color: "#C90000",
    iconLabel: "Closed",
    styleName: "caseStatusClosedIcon",
    style: {
      color: "white",
      transform: "scale(0.7)",
    },
  },
};

// AwayStatus | SkypeCircleClock
const StatusIcon = ({ status }) => {
  const { iconName, color, iconLabel, style } = iconStyles[status];
  return (
    <div className={styles.caseCardWrapper}>
      <span className={styles.caseStatusIcon} style={{ backgroundColor: color }}>
        <Icon iconName={iconName} style={style} />
      </span>
      <Text className={styles.caseStatusText} as="p">
        {iconLabel}
      </Text>
    </div>
  );
};

const CaseStatus = ({ caseStatus }) => {
  return <StatusIcon status={caseStatus} />;
};

export default CaseStatus;
