import { Text } from "@fluentui/react";
import React from "react";
import ButtonWithIcon from "../lib/generic/button/ButtonWithIcon";
import AreaField from "../lib/generic/customField/AreaField";
import PinnedLabel from "../lib/generic/PinnedLabel";
import styles from "./CustomCardLayout.module.css";

const areaTitleStyles = { fontSize: "20px", color: "#A5A5A5", marginBottom: "10px" };

const areaFieldsWrapper = {
  display: "grid",
  gridTemplateColumns: "35% 50% auto",
  gap: "14px",
};

const AreaRenderer = (props) => {
  const { areaName } = props;
  return (
    <div className={styles.areaFlexWrapper}>
      <Text style={areaTitleStyles} as="h4">
        {areaName}
      </Text>
      <div style={{ marginLeft: "10px" }}>
        <PinnedLabel labelOne="Field" labelTwo="Style" gridGap="14px" />
        <div style={areaFieldsWrapper}>
          <AreaField />
          <AreaField />
          <AreaField />
          <AreaField />
        </div>
        <div style={{ marginTop: "20px" }}>
          <ButtonWithIcon iconName="Add" name="Add field" />
        </div>
      </div>
    </div>
  );
};

export default AreaRenderer;
