import React from "react";
import styles from "./CaseCard.module.css";

import ContactCard from "../contactCard/ContactCard";

const peopleOne = [
  {
    firstName: "Firstname",
    lastName: "LastName",
    role: "Matter Billing Lawyer",
    presence: "online",
  },
  {
    firstName: "Firstname",
    lastName: "LastName",
    role: "Advocate",
    presence: "offline",
  },
];

const peopleTwo = [
  {
    firstName: "Firstname",
    lastName: "LastName",
    role: "Advocate",
    presence: "away",
  },
  {
    firstName: "Firstname",
    lastName: "LastName",
    role: "Advocate",
    presence: "busy",
  },
  {
    firstName: "Firstname",
    lastName: "LastName",
    role: "Advocate",
    presence: "dnd",
  },
];

const CaseSectionThree = () => {
  return (
    <div className={styles.sectionThree}>
      {peopleOne.map((contactDetails, index) => {
        const keyName = `Cell_AREA_FOUR_HIGHLIGHTED_${index + 1}`;
        return <ContactCard key={keyName} contactDetails={contactDetails} highlighted />;
      })}
      {peopleTwo.map((contactDetails, index) => {
        const keyName = `Cell_AREA_FOUR_${index + 1}`;
        return <ContactCard key={keyName} contactDetails={contactDetails} />;
      })}
    </div>
  );
};

export default CaseSectionThree;
